import React,{useEffect,useState} from 'react';
import Navbar from "./components/Navbar/Navbar"
import './App.css';
import Login from "./components/Login/Login"
import AdminRoute from './components/Routing/AdminRoute'
import UserRoute from './components/Routing/UserRoute'
import LandingPage from './pages/LandingPage/LandingPage'
import Category from "./pages/Kategori/Kategori";
import Satuan from "./pages/Satuan/Satuan"
import Sales from './pages/Sales/Sales';
import Tentang from './pages/Informasi/Tentang'
import Item from './pages/Item/Item'
import { setAuthToken } from "./config/api";
import { Provider } from "react-redux";
import { loadUser } from "./redux/action/auth";
import ItemAdmin from "./pages/ItemAdmin/ItemAdmin";
import store from "./redux/store/store";
import ModalLogin from "./components/Login/Login"
import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

if (localStorage.token) {
  setAuthToken(localStorage.token);
}

function App() {

  useEffect(() => {
    store.dispatch(loadUser());
    }, []);

  const [HandleLogin,setHandleLogin]=useState(false)
  return (
    <Provider store={store}>
      <div className="App">    
          <Router>  
            <div>
               <Navbar />
            </div>            
              <div className="Container"> 
                <div  className="page">
                <Switch>
                  <Route path="/">
                          <LandingPage HandleLogin={HandleLogin} setHandleLogin={setHandleLogin}/>
                  </Route>
                  <Route exact path="/tentang" component={Tentang}/>
                  <Route exact path="/item" component={Item}/>
                  <Route exact path="/suplier" component={Sales}/>
                  <Route exact path="/kategori" component={Category}/>
                  <Route exact path="/satuan" component={Satuan}/>
                  <Route exact path="/item-admin" component={ItemAdmin}/>
                </Switch> 
                </div>  
              </div>              
          </Router>
          {HandleLogin && <ModalLogin HandleLogin={HandleLogin} setHandleLogin={setHandleLogin}/>}     
      </div>
    </Provider>
  );
}

export default App;
